import React from 'react';
import './App.css';
import './index.css';
import { Button,Icon,Modal,Label,Input } from 'semantic-ui-react'
import QrContainer from './component/QRContainer'
import HutangScreen from './component/HutangScreen';

function App() {
  function exampleReducer(state, action) {
    switch (action.type) {
      case 'CLEAR_LOG':
        return { ...state, log: [] }
      case 'OPEN_MODAL':
        return {
          log: [
            {
              event: action.event,
              date: new Date().toLocaleTimeString(),
              name: action.name,
              value: true,
            },
            ...state.log,
          ],
          open: true,
        }
      case 'CLOSE_MODAL':
        return {
          log: [
            {
              event: action.event,
              date: new Date().toLocaleTimeString(),
              name: action.name,
              value: true,
            },
            ...state.log,
          ],
          open: false,
        }
      default:
        throw new Error()
    }
  }

  const [state, dispatch] = React.useReducer(exampleReducer, {
    log: [],
    open: false,
  })

  const { log, open } = state

  return (
    <div className="App">
      <header className="App-header">
        <div className="Header-container">
          <h1>Hutang</h1>
          <h5>A simple way to <span style={{textDecorationLine: 'line-through'}}>take money</span> manage your borrowing with friends.</h5>
        </div>
        <div className="Body-container">
          <Button 
            style={{ margin: 10 }} color="green" 
            icon labelPosition='left' size="massive"
            onClick={(e) =>
              dispatch({
                event: e.type,
                name: 'onClick',
                type: 'OPEN_MODAL',
              })
            }>
            <Icon name="money bill alternate"/>
            Kasi Hutang Lah!
          </Button>
          <Button style={{ margin: 10 }}  color="blue" icon labelPosition='left' size="massive">
            <Icon name="signing"/>
            Bayarlah Hutang!
          </Button>
          <Button style={{ margin: 10 }}  color="teal" icon labelPosition='left' size="massive">
            <Icon name="payment"/>
            Ampun Bang Jago!
          </Button>
          <Button style={{ margin: 10 }} className="btn-content" color="orange" icon labelPosition='left' size="massive">
            <Icon name="history"/>
            Aiyo Sudah Lupa!
          </Button>
        </div>
        <div className="Bottom-container">
          <h6>
          <Icon name="coffee"/>Developed with love by Amir.
          </h6>
        </div>
        <Modal
          onOpen={(e) =>
            dispatch({ event: e.type, name: 'onOpen', type: 'OPEN_MODAL' })
          }
          onClose={(e) =>
            dispatch({ event: e.type, name: 'onClose', type: 'CLOSE_MODAL' })
          }
          open={open}
          dimmer={"blurring"}>
          <Modal.Header className="modal-header">
            Add new hutang
            <Label as='a' image>
              <img src='https://react.semantic-ui.com/images/avatar/small/joe.jpg' />Amir
            </Label>
          </Modal.Header>
          <Modal.Content className="modal-cont">
            <Icon color="green" name="money bill alternate"/> How much was it?
            <br/>
            <Input placeholder="Amount e.g. 250.00"/>
            <br/>
            <br/>
            Choose the list of contacts for this bill.
          </Modal.Content>
        </Modal>
      </header>
    </div>
  );
}

export default App;
