import React, { Component } from 'react'
import QrReader from 'react-qr-scanner'
import { Modal } from 'semantic-ui-react'

export default class HutangScreen extends Component {

    constructor(props){
        super(props)
        this.state = {
            userID: ''
        }
    }

    render(){

        const [open, setOpen] = React.useState(false)

        return(
            <Modal
                onClose={() => setOpen(false)}
                onOpen={() => setOpen(true)}
                open={open}>
                <Modal.Header>Adding Hutang as <code>{this.state.userID}</code></Modal.Header>
                <Modal.Content>

                </Modal.Content>
            </Modal>
        )
    }
}